/**
 * Navbar
 */

$navbar-color-bg: #fff;
$navbar-color-link: $color-link-color;
$navbar-color-link-hover: $color-link-color-hover;

.navbar {
  background-color: $navbar-color-bg;
  z-index: 100;

  &:before {
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
    content: "";
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 10px;
    z-index: 10;
  }
}

.navbar__wrapper {
  display: flex;
  align-items: center;
}

.navbar__brand {
  margin: 0 auto;

  &:hover {
    text-decoration: none;
    color: $navbar-color-link-hover;
  }
}

.navbar__search {
  display: block;
  margin: 0 auto 0 12px;
}

.navbar__toggler {
  display: none;

  &:focus {
    outline: none;
  }
}

.navbar__menu {
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar__item {
  display: block;
  margin: 0;
  padding: 18px 20px;
  position: relative;
  @include media-breakpoint-down(xs) {
    &.is--active:not(.navbar__subitem) {
      padding: 0;

      & > .navbar__title {
        height: 36px;
        line-height: 36px;
        display: block;
        background-color: $color-black;
        color: $color-white;
        text-align: center;
        position: relative;

        & .navbar__arrow {
          border-color: $color-white;
          position: absolute;
          transform: rotate(135deg);
          top: 14px;
          right: 20px;
        }
      }
    }

    &.is--hidden:not(.navbar__subitem) {
      display: none;
    }

    &.navbar__subitem {
      border-bottom: 1px solid #ccc;

      &:last-of-type {
        border: none;
      }

      &.is--active {
        & .navbar__hide {
          display: block;
        }

        & .navbar__show {
          display: none;
        }
      }

      & .navbar__menu {
        padding: 0;
      }
    }
  }
}

.navbar__title {

  color: $navbar-color-link;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 600;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.navbar__arrow {
  border: solid black;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 2px 2px 3px 3px;
  transform: rotate(-45deg);
  max-height: 8px;
  max-width: 4px;
}

.navbar__item--selected .navbar__title {
  color: $navbar-color-link-hover;
}

.navbar__cart {
  margin: 0 0 0 auto;
}

.navbar__account {
  margin: 0 0 0 12px;

}

.navbar__arrow--sub {
  font-style: normal;
}

.navbar__item > .navbar__menu {
  display: none;
  padding: 0 20px;
}

.navbar__item.is--active > .navbar__menu {
  display: block;
}

.navbar__item > .navbar__menu > .navbar__item:not(.navbar__subitem) {
  display: block;
  padding: 16px 0;
}

.is--active > .navbar__show,
.navbar__hide {
  display: none;
}

@include media-breakpoint-down(xs) {
  .navbar__wrapper {
    height: $navbar-height-mobile;
  }
  .navbar__collapse,
  .navbar__toggler,
  .navbar__toggler-link {
    transition: $transition-base;
  }
  .navbar__collapse {
    opacity: 0;
    visibility: hidden;
    position: fixed;
    top: $navbar-height-mobile;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: calc( 100% - 56px );
    overflow-x: hidden;
    overflow-y: auto;
    pointer-events: auto;
  }
  .navbar__toggler {
    display: inline-block;
    position: relative;
    margin: 0;
    padding: 0;
    background: none;
    border: none;
    border-radius: 0;
    width: 18px;
    height: 12px;
    cursor: pointer;
    z-index: 110;
  }
  .navbar__toggler-line {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    background-color: black;
    height: 2px;
    width: 18px;

    &:nth-child(2) {
      top: 6px;
    }

    &:nth-child(3) {
      top: 12px;
    }
  }
  .navbar__menu {
    background-color: $navbar-color-bg;
  }
  body.navbar--opened {
    // Prevent vertical scrollbar when navbar is opened on smaller devices
    overflow: hidden;

    .navbar__collapse {
      visibility: visible;
      opacity: 1;
    }

    .navbar__toggler-line {
      &:nth-child(1) {
        top: 4px;
        transform: rotate(45deg);
      }

      &:nth-child(2) {
        opacity: 0;
      }

      &:nth-child(3) {
        top: 4px;
        transform: rotate(-45deg);
      }
    }

    .navbar__toggler {
      &:hover {
        transform: rotate(90deg);
      }
    }
  }
}

// Media: Medium breakpoint and up
@include media-breakpoint-up(sm) {
  .navbar__wrapper {
    height: $navbar-height-desktop;
    position: relative;
  }
  .navbar__brand {
    margin: 0 auto 0 0;
  }
  .navbar__menu {
    flex-direction: row;
    display: flex;
    margin: 0 -20px;
  }
  .navbar__item {
    padding: 0;
    margin: 0 20px;
    position: relative;
transition: $transition-base;
    &.is--hidden:not(.navbar__subitem) {
      display: block;
    }

    & .navbar__title {
      cursor: pointer;
      font-size: 14px;
      position: relative;

      &:before {
        content: "";
        display: block;
        position: absolute;
        bottom: -24px;
        width: 100%;
        background-color: transparent;
        height: 4px;
        z-index: 10;
        transition: $transition-base;
      }

      & .navbar__arrow {
        display: none;
      }
    }


    &.is--active {
      &:not(.navbar__subitem) > .navbar__title {
        position: relative;

        &:before {
          background-color: $color-black;
        }
      }

      & .navbar__menu {
        display: block;
      }

      & > .navbar__menu {
        position: fixed;
        width: 98%;
        background: $color-white;
        box-shadow: 0 2px 20px rgba(0, 0, 0, 0.25);
        top: 80px;
        padding: 24px 75px;
        display: flex;
        justify-content: space-between;
        left: 50%;
        margin-left: -49vw;

      }
    }

    &.navbar__subitem {
      margin: 0;
      padding: 0;

      & .navbar__title {
        pointer-events: none;

        & .navbar__arrow--sub {
          display: none;
        }
      }

      &:first-of-type .navbar__menu {
        columns: 2;
      }

      & .navbar__menu {
        position: relative;
        padding: 0;
        margin: 0;

        & .navbar__item {
          margin: 0;
          padding: 14px 0 0 0;
          font-size: 14px;
          display: block;
          & .navbar__link:hover{
            font-weight: 600;
          }
        }
      }
    }
  }
  .navbar__search {
    position: absolute;
    right: 60px;
    margin: 0;
  }
}

// Media: Medium breakpoint and up
@include media-breakpoint-up(lg) {

  .navbar__menu {
    margin: 0 -47px;
  }
  .navbar__item {
    margin: 0 47px;

    &.is--active {
      & > .navbar__menu {
        width: 900px;
        max-width: 900px;
        left: 50%;
        margin-left: -450px;
      }
    }
  }
}

@include media-breakpoint-up(xl) {
  .navbar__item.is--active > .navbar__menu {
    width: 1072px;
    max-width: 1072px;
    left: 50%;
    margin-left: -536px;
  }
}
