/**
 * Buttons
 */

$btn-color-bg: $color-base !default;
$btn-color-bg-hover: #fff !default;

$btn-color-text: white !default;
$btn-color-text-hover: $color-base !default;

$btn-font-weight: 500 !default;
$btn-font-size: 16px !default;
$btn-font-size-sm: 12px !default;

$btn-padding-x: 92px !default;
$btn-padding-x-sm: 50px !default;
$btn-padding-y: 0 !default;

$btn-border-color: transparent !default;
$btn-border-color-hover: transparent !default;
$btn-border-width: 1px !default;
$btn-border-radius: 4px !default;

$btn-sm: .75 !default;
$btn-lg: 1.25 !default;

$btn-height: 52px;
$btn-height-sm: 40px;
$btn-box-shadow: 0 9px 14px rgba(0, 0, 0, 0.25);
$btn-box-shadow-active: none;

.btn {
  display: inline-block;
  padding: $btn-padding-y $btn-padding-x-sm;
  border: $btn-border-width solid transparent;
  background-color: $btn-color-bg;
  color: $btn-color-text;
  vertical-align: middle;
  text-align: center;
  white-space: nowrap;
  text-transform: uppercase;
  font-size: $btn-font-size-sm;
  font-weight: $btn-font-weight;
  height: $btn-height-sm;
  line-height: $btn-height-sm;
  cursor: pointer;
  transition: $transition-base;
  user-select: none;
  text-decoration: none;
  border-radius: $btn-border-radius;
  box-shadow: $btn-box-shadow;

  &:hover,
  &:focus {
    text-decoration: none;
  }

  &:hover {
    background-color: $btn-color-bg-hover;
    color: $btn-color-text-hover;
  }
  &:active {
    background-color: $btn-color-bg-hover;
    color: $btn-color-text-hover;
    box-shadow: $btn-box-shadow-active;
  }

  &:focus {
    outline: 0;
  }
}

// Size Variants
.btn-sm {
  font-size: $btn-font-size * $btn-sm;
  padding: ($btn-padding-y * $btn-sm) ($btn-padding-x * $btn-sm);
}
.btn-lg {
  font-size: $btn-font-size * $btn-lg;
  padding: ($btn-padding-y * $btn-lg) ($btn-padding-x * $btn-lg);
}

// Block
.btn-block {
  display: block;
  width: 100%;
}

// Disabled
.btn:disabled {
  opacity: .65;
  pointer-events: none;
}


.btn-black {
  background-color: $color-black;
  &:hover {
    background-color: lighten($color-black, 100%);
  }
  &.btn-outline {
    background-color: transparent;
    border-color: $color-black;
    color: $color-black;
    &:hover {
      background-color: lighten($color-black, 10%);
      border-color: lighten($color-black, 10%);
      color: $color-white;
    }
  }
  &.btn-link {
    background-color: transparent;
    color: $color-black;
    &:hover {
      color: lighten($color-black, 10%);
    }
  }
}

@include media-breakpoint-up(sm){
  .btn{
    height: $btn-height;
    line-height: $btn-height;
    font-size: $btn-font-size;
    padding: $btn-padding-y $btn-padding-x;

  }
}
