/**
*
Carousel
 **/
$carousel-bg-1: #95cfd5;
$carousel-bg-2: #e0adc5;
$carousel-bg-3: #c42129;
$carousel-transition: all 1.2s linear;
.carousel {
  width: 100%;
  background-color: $carousel-bg-1;
  min-height: calc(100vh - 56px);
  overflow: hidden;
  display: flex;
  align-items: center;
  transition: $carousel-transition;
}

.carousel[data-carousel-index="2"] {
  background-color: $carousel-bg-2;
}

.carousel[data-carousel-index="3"] {
  background-color: $carousel-bg-3;
}

.carousel__title {
  white-space: nowrap;
}

.carousel__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
}

.carousel__col {
  width: 100%;
  text-align: center;
  position: relative;
}

@include media-breakpoint-down(md) {
  .carousel__col:last-of-type {
    min-height: 242px;
  }
}

.carousel__item {
  visibility: hidden;
  opacity: 0;
  position: absolute;
  transition: $carousel-transition;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;

  &.is--active {
    opacity: 1;
    visibility: visible;
  }

  &.is--fadeout {
    opacity: 0;
    visibility: hidden;
  }
}

.carousel__images-dots {
  display: none;
}

.carousel__item--img {
  position: relative;
  max-height: 212px;
  display: block;
  margin: 24px auto 0 auto;
  transform: translateX(-10vh);
  transition: $carousel-transition;
  z-index: 2;
}

.carousel__item.is--active .carousel__item--img {
  transform: translateX(0);
}

.carousel__item.is--fadeout .carousel__item--img {
  transform: translateX(100vh);
}

.carousel__item--text {
  position: absolute;
  max-width: 47px;
  top: 100vh;
  left: 50%;
  transform: translate(54px, -50%);
  transition: $carousel-transition;
  z-index: 1;
}

.carousel__item.is--active .carousel__item--text {
  top: 50%;
}

.carousel__item.is--fadeout .carousel__item--text {
  top: -160vh;
}

.carousel__video {
  margin-left: 16px;
}

.carousel__video--text {
  display: none;
}

.carousel__actions {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-top: 16px;
}

.carousel__video--button {
  cursor: pointer;
  display: flex;
  transition: $transition-base;
  width: 38px;
  height: 38px;
  border-radius: 38px;
  border: 1px solid $color-black;
  position: relative;
  justify-content: center;
  align-items: center;
}

.carousel__video--button:before {
  content: "";
  display: block;
  transition: $transition-base;
  width: 22px;
  height: 22px;
  background: $color-black;
  border-radius: 22px;
}

.carousel__video--button:after {
  content: "";
  display: block;
  border-left: 4px solid white;
  border-top: 3px solid transparent;
  border-bottom: 3px solid transparent;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.carousel__dots{
  position: absolute;
  left: 0;
  right: 0;
  bottom: -32px;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}
.carousel__dots--item{
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 10px;
  height: 10px;
  border: 1px solid $color-white;
  border-radius: 10px;
  margin: 0 4px;
  &.is--active:before{
    background: $color-white;
  }
  &:hover:before{
    background: $color-white-hover;
  }
}
.carousel__dots--item:before{
  content: '';
  width: 6px;
  height: 6px;
  border-radius: 6px;
  background: transparent;
  transition: $transition-base;
}

@include media-breakpoint-up(lg) {
  .carousel {
    min-height: calc(100vh - 70px);
  }

  .carousel__wrapper {
  }

  .carousel__col {
    width: 50%;
    text-align: left;
    position: relative;
    min-height: 412px;
  }

  .carousel__item {
    &.is--active {
    }
  }

  .carousel__images-dots {
    display: flex;
    list-style: none;
    padding: 0;
    margin: 64px -8px 0 -8px;
  }
  .carousel__images-dots__item {
    margin: 0 8px;
    border: 1px solid white;
    width: 80px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 7px;
    cursor: pointer;
    transition: $transition-base;

    &:hover {
      background: rgba(255, 255, 255, .5);
    }

    &.is--active {
      background: white;
    }
  }

  .carousel__item--img {
    max-height: 412px;
    margin: 0 auto;
  }

  .carousel__item--text {
    position: absolute;
    max-width: 90px;
    transform: translate(104px, -50%);
  }

  .carousel__actions {
    margin-top: 36px;
    justify-content: flex-start;
  }
  .carousel__video {
    margin-left: 46px;
    display: flex;
    align-items: center;
    cursor: pointer;

    &:hover &--text {
      border-bottom-color: $color-black;
    }

    &:hover &--button:before {
      background: $color-white;
    }

    &:hover &--button:after {
      border-left-color: $color-black;
    }
  }
  .carousel__video--text {
    margin-left: 8px;
    font-size: 16px;
    display: inline-block;
    border-bottom: 1px solid transparent;
    line-height: 1.2;
    transition: $transition-base;
  }
  .carousel__video--button {
    width: 50px;
    height: 50px;
    border-radius: 50px;

    &:before {
      width: 28px;
      height: 28px;
      border-radius: 22px;
      transition: $transition-base;
    }

    &:after {
      border-left: 4px solid white;
      border-top: 3px solid transparent;
      border-bottom: 3px solid transparent;
      transition: $transition-base;
    }
  }
  .carousel__dots{
    top: 50%;
    transform: translateY(-50%);
    display: block;
    right: 0;
    bottom: auto;
    left: auto;
  }
  .carousel__dots--item{
    margin: 4px 0;
  }
}

@include media-breakpoint-up(xl) {
  .carousel__col {
    min-height: 542px;
  }
  .carousel__item--img {
    max-height: 542px;
  }
  .carousel__item--text {
    max-width: 120px;
    transform: translate(144px, -50%);
  }
  .carousel__col:first-of-type {
    padding-left: 80px;
  }
}
